<template>
    <div class="add-payment">
        <div class="add-c">
            <div class="asset-title">
                <span class="desc"></span>
                <span class="name">{{$t('center.addPayment[0]')}}</span>
            </div>
            <div class="form-warp">
                <p class="label">{{$t('center.addPayment[0]')}}</p>
                <template>
                    <el-select v-model="value" :disabled="isEdit"  :placeholder="$t('center.addPayment[1]')" @change="changePayment">
                        <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
                <!-- 添加微信支付 -->
                <div class="wechat" v-show="isShow == 1">
                    <p class="label">{{$t('center.addPayment[2]')}}</p>
                    <el-input :disabled="true" v-model="form.name"></el-input>
                    <p class="label" >{{$t('center.addPayment[3]')}}</p>
                    <el-input :placeholder="$t('center.addPayment[4]')" v-model="form.wx"></el-input>
                    <p class="label">{{$t('center.addPayment[5]')}}</p>
                    <el-upload
                    class="avatar-uploader"
                    :on-change="handleChange"
                    :action="baseUrl + imgUpload"
                    :on-success="handleAvatarSuccess">
                        <img v-show="showImg == 0" v-if="imageUrl" :src="imageUrl" class="avatar">
                        <img v-show="showImg == 1" v-if="imageUrl" :src="baseUrl + imageUrl" class="avatar">
                        <div v-if="!imageUrl" class="code-warp">
                            <img src="../../../assets/img/center/add_code.png" alt="" class="add-img">
                            <p class="desc">{{$t('center.addPayment[6]')}}</p>
                        </div>
                    </el-upload>
                    <p class="label">{{$t('center.addPayment[7]')}}</p>
                    <el-input type="password" :placeholder="$t('center.addPayment[8]')" v-model="form.tpwd"></el-input>
                </div>
                <!-- 添加支付宝支付 -->
                <div class="wechat" v-show="isShow == 2">
                    <p class="label">{{$t('center.addPayment[9]')}}</p>
                    <el-input :disabled="true" v-model="form.name"></el-input>
                    <p class="label">{{$t('center.addPayment[10]')}}</p>
                    <el-input :placeholder="$t('center.addPayment[11]')" v-model="form.alipay"></el-input>
                    <p class="label">{{$t('center.addPayment[12]')}}</p>
                    <el-upload
                    class="avatar-uploader"
                    :on-change="handleChange"
                    :action="baseUrl + imgUpload"
                    :on-success="handleAvatarSuccess">
                        <img v-show="showImg == 0" v-if="imageUrl" :src="imageUrl" class="avatar">
                        <img v-show="showImg == 1" v-if="imageUrl" :src="baseUrl + imageUrl" class="avatar">
                        <div v-if="!imageUrl" class="code-warp">
                            <img src="../../../assets/img/center/add_code.png" alt="" class="add-img">
                            <p class="desc">{{$t('center.addPayment[13]')}}</p>
                        </div>
                    </el-upload>
                    <p class="label">{{$t('center.addPayment[7]')}}</p>
                    <el-input type="password" :placeholder="$t('center.addPayment[14]')" v-model="form.tpwd"></el-input>
                </div>
                <!-- 添加信用卡支付 -->
                <div class="card" v-show="isShow == 3">
                    <p class="label">{{$t('center.addPayment[9]')}}</p>
                    <el-input placeholder="请输入开户名" :disabled="true" v-model="form.name"></el-input>
                    <p class="label">{{$t('center.addPayment[15]')}}</p>
                    <el-input :placeholder="$t('center.addPayment[16]')" v-model="form.bank"></el-input>
                    <p class="label">{{$t('center.addPayment[17]')}}</p>
                    <el-input :placeholder="$t('center.addPayment[18]')" v-model="form.branch "></el-input>
                    <p class="label">{{$t('center.addPayment[19]')}}</p>
                    <el-input :placeholder="$t('center.addPayment[20]')" v-model="form.bank_car"></el-input>
                    <p class="label">{{$t('center.addPayment[7]')}}</p>
                    <el-input type="password" :placeholder="$t('center.addPayment[8]')" v-model="form.tpwd"></el-input>
                </div>
                <div class="button-group clr">
                    <el-button class="btn add-btn" @click="addPayment">{{$t('center.addPayment[21]')}}</el-button>
                    <el-button class="btn cancel-btn">{{$t('center.addPayment[22]')}}</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>    
import { paymentAdd, paymentList } from '../../../api/c2c'
export default {
    name: 'addPayment',
    data() {
        return {
            showImg: 1,
            form: {
                act: '',
                type: '',
                tpwd: '',
                pal_account: '',
                bank: '',
                branch: '',
                bank_car: '',
                wx_img: '',
                ali_img: '',
                name: "",
                alipay: '',
                wx: ''
            },
            isEdit: false,
            formData: {},
            imageUrl: '',
            imgUpload: this.$public.imgUpload,
            baseUrl: this.$public.baseUrl,
            isShow: 1,
            options: [{
                value: '1',
                label: this.$t('center.addPayment[23]')
            }, {
                value: '2',
                label: this.$t('center.addPayment[24]')
            }, {
                value: '3',
                label: this.$t('center.addPayment[25]')
            }],
            value: '1',
            img: ''
        }
    },
    created() {
        if (this.$route.params.type) {
            this.getPaymentData()
            this.isEdit = true //修改支付方式  禁用el-select选择器
        }
        this.form.name =  JSON.parse(sessionStorage.userInfo).name
    },
    methods: {
        handleChange() {
            this.showImg = 1
        },
        getPaymentData() {
            paymentList().then(res=> {
                if (res.data.status == 200) {
                    for (var i=0; i<res.data.data.length; i++) {
                        if (res.data.data[i].type == this.$route.params.type) {
                            if (res.data.data[i].type == 'wx') {
                                this.value = '1'
                                this.isShow = '1'
                                this.form.name = res.data.data[i].name
                                this.form.wx = res.data.data[i].number
                                this.imageUrl = res.data.data[i].img
                                this.showImg = 0
                            } else if (res.data.data[i].type == 'alipay') {
                                this.value = '2'
                                this.isShow = '2'
                                this.form.name = res.data.data[i].name
                                this.form.alipay = res.data.data[i].number
                                this.imageUrl = res.data.data[i].img
                                this.showImg = 0
                            } else if (res.data.data[i].type == 'backcard') {
                                this.value = '3'
                                this.isShow = '3'
                                this.form.name = res.data.data[i].name
                                this.form.bank = res.data.data[i].bank
                                this.form.branch = res.data.data[i].branch
                                this.form.bank_car = res.data.data[i].number
                            }   
                        }
                    }
                }
            })
        },
        handleAvatarSuccess(res) {
            this.imageUrl = res.data.url
        },
        // 确认添加/修改支付方式
        addPayment() {
            this.$public.googleVerify().then((r)=> {
                if (this.form.tpwd == '') {
                    this.$public.msg(this.$t('center.addPayment[26]'),'error',this)
                    return
                } else if (!this.$public.pwd(this.form.tpwd)) {
                    this.$public.msg(this.$t('center.addPayment[27]'),'error',this)
                    this.form.tpwd = ''
                    return
                } else {
                    if (this.value == 1) {//wx
                        if (!this.imageUrl) {
                            this.$public.msg(this.$t('center.addPayment[28]'),'error',this)
                            return
                        } 
                        var changeForm = {
                            wx_img: this.imageUrl,
                            type: 'wx',
                            wx: this.form.wx,
                            tpwd: this.$public.Cryptographiccapital(this.form.tpwd),
                            act: '',
                            name: this.form.name
                        }
                    } else if (this.value == 2) {//alipay
                        if (!this.imageUrl) {
                            this.$public.msg(this.$t('center.addPayment[29]'),'error',this)
                            return
                        } 
                        var changeForm = {
                            ali_img: this.imageUrl,
                            type: 'alipay',
                            alipay: this.form.alipay,
                            tpwd: this.$public.Cryptographiccapital(this.form.tpwd),
                            act: '',
                            name: this.form.name
                        }
                    } else if (this.value == 3) {//bank
                        var changeForm = {
                            act: '',
                            type: 'backcard',
                            tpwd: this.$public.Cryptographiccapital(this.form.tpwd),
                            pal_account: this.form.pal_account,
                            bank: this.form.bank,
                            branch: this.form.branch,
                            bank_car: this.form.bank_car,
                            name: this.form.name
                        }
                    }
                    if (this.$route.params.type) {
                        changeForm.act = 'edit'
                    } else {
                        changeForm.act = 'add'
                    }
                    var postData = JSON.parse(JSON.stringify(changeForm))
                    paymentAdd(postData).then(res=> {
                        if (res.data.status == 200) {
                            this.$public.msg(res.data.msg, 'success', this)
                            this.$router.push({ name: 'paymentMethod' })
                        } else {
                            this.$public.msg(res.data.msg, 'error', this)
                            return
                        }
                    })
                }
            }).catch((r)=>{})
        },
        // 选择添加的支付方式
        changePayment() {
            this.isShow = this.value
            // if(this.isShow == 3){
            //     this.form.name =  ""
            // }else{
                this.form.name = JSON.parse(sessionStorage.userInfo).name
            // }
        }
    }
}
</script>

<style lang=less>
  @import '../../../assets/index.less';
  @import './addPayment.less';
</style>


